<script>
import Scroll from "../../../components/common/scroll/Scroll.vue";
import { $getListByUser } from "../../../network/adminTools/order";
import OrderList from "./childComponents/orderList/OrderList.vue";
import EmptyDataPlaceholder from "../../../components/common/emptyDataPlaceholder/EmptyDataPlaceholder.vue";
import $ from "jquery";
import { UrlDecode } from "../../../common/utils";

export default {
  name: "e-ReviewProgress",
  /**
   * 组件内路由守卫
   */
  beforeRouteEnter(to, from, next) {
    if (window.localStorage.getItem("isStaff") === "true") {
      window.localStorage.setItem("currentIndex010001", -1);
      next();
    } else {
      next({
        path: "/tools/login",
      });
      window.localStorage.removeItem("list");
      window.localStorage.removeItem("order");
      window.localStorage.removeItem("currentIndex010001");
      window.localStorage.removeItem("userID");
      window.localStorage.removeItem("username");
      window.localStorage.removeItem("authToken");
      window.localStorage.removeItem("isStaff");
      window.localStorage.removeItem("isSuperUser");
      window.localStorage.removeItem("isProvider");
      window.localStorage.removeItem("providerID");
    }
  },
  /**
   * 每一次组件处于活跃状态时
   */
  async created() {
    if (this.$refs.scroll) {
      this.$refs.scroll.refresh();
    }
    // const userid = Number(this.cookieOperations.getCookie('userId'));
    const requestPayload = {
      user: {
        id: Number(window.localStorage.getItem("userID")),
      },
    };
    this.isLoading = true;
    const response = await $getListByUser(requestPayload);
    const { Code, msg, details } = response;
    console.log("审核进度 开局list");
    console.log(response);
    // 获取当前登录用户所有工单数据
    if (Code === 200 && msg === "success") {
      this.listAll = details; // 将数据赋值给listAll
      this.totalPage = Math.ceil(this.listAll.length / this.pageSize); // 重新计算totalPage
      const tempListAll = window.JSON.parse(
        window.JSON.stringify(this.listAll)
      );
      console.log(tempListAll);
      // 若listShow数组为空，则说明是首次加载组件
      if (this.listShow.length === 0) {
        this.listShow = tempListAll.slice(0, this.pageSize);
      }
      // 若进来页面时分页的currentPage和分页的totalPage相等，因为listAll的数据有可能进行了更新，所以需要更新listShow
      if (this.currentPage === this.totalPage) {
        this.listShow = tempListAll.slice(
          this.pageSize * (this.currentPage - 1),
          this.pageSize * this.currentPage
        );
      }
    } else {
    }

    this.isLoading = false;

    this.$bus.$on("viewDetailsClick", (param) => {
      const { x, y, index } = param;

      this.x = x;
      this.y = y;
      const elex = this.x;
      const eley = this.y;

      const vm = this;
      const str = `.details${index}`;
      this.isShowCoverPage = true;
      // 计算第二个动画要移动到哪个点
      let targetLeft;
      const clientWidth = document.body.clientWidth;
      if (clientWidth < 1000) {
        targetLeft = 0;
      } else if (clientWidth > 1000 && clientWidth < 1240) {
        targetLeft = (clientWidth - 1000) / 2;
      } else {
        targetLeft = 240 + (clientWidth - 240 - 1000) / 2;
      }

      let targetTop;
      let clientHeight = document.body.clientHeight;
      if (clientHeight < 680) {
        targetTop = 0;
      } else {
        targetTop = (clientHeight - 680) / 2;
      }

      $(str).animate({ left: elex, top: eley }, 50, function () {
        vm.showIndex = index;
      });
      $(str).animate(
        { left: targetLeft, top: targetTop, width: "100px", height: "100px" },
        100,
        function () {
          vm.$refs.roll[index].refresh();
          vm.$refs.roll[index].scrollTo(0, 0, 0);
        }
      );
      $(str).animate({ width: "1000px", height: "680px" }, 150);
    });
  },
  /**
   * 当组件处于非活跃状态时
   */
  deactivated() {
    this.$bus.$off("viewDetailsClick");
  },
  data() {
    return {
      listAll: [],
      listShow: [],
      pageSize: 8,
      currentPage: 1,
      totalPage: 0,
      isShowCoverPage: false,
      showIndex: -1,
      x: null,
      y: null,
    };
  },
  methods: {
    VMUrlDecode(str) {
      return UrlDecode(str);
    },
    hideViewDialog() {
      const str = `.details${this.showIndex}`;
      const vm = this;
      $(str).animate({ width: "70px", height: "70px" }, 50);
      $(str).animate(
        { left: vm.x, top: vm.y, width: "0px", height: "0px" },
        200,
        function () {
          vm.isShowCoverPage = false;
          vm.showIndex = -1;
        }
      );
    },
  },
  watch: {
    currentPage(newVal) {
      const tempListAll = window.JSON.parse(
        window.JSON.stringify(this.listAll)
      );
      this.listShow = tempListAll.slice(
        this.pageSize * (newVal - 1),
        this.pageSize * newVal
      );
      this.$refs.scroll.scrollTo(0, 0, 400);
      setTimeout(() => {
        this.$refs.scroll.refresh();
      }, 200);
    },
    listShow: {
      handler(newVal) {},
      deep: true,
    },
  },
  components: {
    Scroll,
    OrderList,
    EmptyDataPlaceholder,
  },
};
</script>

